/* RESET */
* { margin: 0; padding: 0; box-sizing: border-box; }
body {  line-height: 1.6; color: #333; overflow-x: hidden; }

.company-name {
  font-family: "Playfair Display", serif;
  font-weight: 700;           /* Bold like the image */
  font-style: normal;
  font-size: 2.6rem;
  color: #ffffff;
  margin: 0;
  line-height: 1.1;
  letter-spacing: 0.5px;
  white-space: nowrap;
}

.company-slogan {
  font-weight: 400;
  font-size: 1.3rem;
  color: #ffeb3b;
  margin-top: 0.3rem;
  white-space: nowrap;
}

@media (max-width: 600px) {
  .company-name {
    font-size: 1.8rem;
  }

  .company-slogan {
    font-size: 1rem;
  }
}

/* ===================== NAVBAR ===================== */
.navbar-container {
  position: absolute;
  top: 5px;
  left: 0;
  width: 100%;
  display: flex;
  align-items: center;
  gap: 6px;
  justify-content: space-between;
  padding: 0 5px;
  z-index: 1000; /* keeps navbar above all other content */
}

* {
  box-sizing: border-box;
}

.navbar,
.navbar-right {
  background-color: rgba(43, 33, 31, 0.85);
  border-radius: 8px;
  backdrop-filter: blur(4px);
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.3);
}

/* âœ… Main navbar layout */
.navbar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px 20px;
  min-height: 80px; /* Same height on desktop */
  width: 100%;
  overflow-x: hidden;
}

/* âœ… Right section */
.navbar-right {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 10px 22px;
  min-height: 88px; /* Match height */
  flex-shrink: 0;
}

/* âœ… Prevent any scroll issues */
body,
html {
  overflow-x: hidden;
  margin: 0;
  padding: 0;
  width: 100%;
}

/* âœ… Responsive layout */
@media (max-width: 768px) {
  .navbar {
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 12px 16px;
    min-height: auto;
  }

  .navbar-right {
    width: 100%;
    padding: 10px;
    min-height: auto;
    margin-top: 8px;
    justify-content: center;
  }
}


.navbar-left {
  display: flex;
  align-items: center;
  color: #f4f0eb;
  font-weight: 600;
  font-size: 16px;
  letter-spacing: 0.3px;
}

.divider {
  width: 1px;
  height: 40px;
  background-color: rgba(255,255,255,0.3);
  margin: 0 20px;
}

.navbar-links {
  display: flex;
  gap: 30px;
}

.navbar-links a {
  color: #f1ede9;
  text-decoration: none;
  font-size: 19px;
  font-weight: 600;
  letter-spacing: 2px;
  transition: color 0.2s ease;
}

.navbar-links a:hover {
  color: #d4a373;
}

.navbar-right {
  text-align: center;
  padding: 10px 22px;
  display: flex;
  justify-content: center; /* center horizontally */
  align-items: center;     /* center vertically */
}

.navbar-right button {
  background: none;
  border: none;
  color: #f1ede9;
  font-size: 13px;
  letter-spacing: 2px;
  cursor: pointer;
}

/* Menu Button */
.menu-btn {
  background-color: rgba(43, 33, 31, 0.85);
  border: 1px solid rgba(255,255,255,0.15);
  color: #f1ede9;
  border-radius: 8px;
  padding: 10px 14px;
  cursor: pointer;
  display: none;
  height: 70px;
  backdrop-filter: blur(4px);
  position: relative;
  z-index: 5;
}

.menu-icon {
  width: 17px;
  height: 22px;
  position: relative;
}
.menu-icon::before,
.menu-icon::after {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  height: 2px;
  background: #f1ede9;
  border-radius: 1px;
  transition: transform 0.3s ease, opacity 0.3s ease;
}
.menu-icon::before { top: 4px; }
.menu-icon::after { bottom: 4px; }
.menu-circle {
  position: absolute;
  top: 8px;
  left: 5px;
  width: 6px;
  height: 6px;
  border: 1.5px solid #f1ede9;
  border-radius: 50%;
  transition: opacity 0.3s ease;
}
.company-name {
  white-space: nowrap;       /* âœ… never wrap to next line */
  overflow: hidden;          /* âœ… hides any overflow */
  text-overflow: ellipsis;   /* âœ… adds "..." if itâ€™s too long */
  font-size: clamp(1rem, 2vw, 1.8rem); /* âœ… responsive font size */
}

/* Active X icon */
.menu-icon.active::before {
  transform: rotate(45deg);
  top: 9px;
}
.menu-icon.active::after {
  transform: rotate(-45deg);
  bottom: 9px;
}
.menu-icon.active .menu-circle { opacity: 0; }

/* Mobile Menu */
.mobile-menu {
  display: none;
  flex-direction: column;
  background-color: rgba(43, 33, 31, 0.95);
  border-radius: 8px;
  margin: 5px;
  padding: 15px 20px;
  z-index: 4;
  position: absolute;
  top: 70px;
  left: 0;
  right: 0;
  animation: fadeIn 0.3s ease;
}

.mobile-menu a {
  color: #f1ede9;
  text-decoration: none;
  font-size: 15px;
  letter-spacing: 2px;
  padding: 10px 0;
}
.mobile-menu a:hover {
  color: #d4a373;
}

/* Mobile Get Started */
.mobile-getstarted {
  background-color: rgba(43, 33, 31, 0.85);
  border-radius: 8px;
  padding: 12px;
  text-align: center;
  margin-top: 12px;
  box-shadow: inset 0 0 2px rgba(255,255,255,0.05);
    border: 1px solid;

}
.mobile-getstarted button {
  border: none;
  background: none;
  color: #f1ede9;
  font-size: 14px;
  letter-spacing: 2px;
  cursor: pointer;
}
.mobile-getstarted button:hover { color: #d4a373; }

@keyframes fadeIn {
  from {opacity: 0; transform: translateY(-10px);}
  to {opacity: 1; transform: translateY(0);}
}

/* Default (Desktop) */
.navbar-logo {
  height: 36px;
  width: auto;
  margin-right: 10px;
}

/* Mobile (â‰¤768px) */
@media (max-width: 768px) {
  .navbar-logo {
    height: 24px;
  }
}


/* Responsive */
@media (max-width: 768px) {
  .navbar-links, .navbar-right { display: none; }
  .menu-btn { display: block; }
}

